@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;900&display=swap);


.grid {
	border: 1px solid #e9d8fd;
	font-size: 0;
}

* {
	touch-action: manipulation;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.node {
	border: 1px solid #e9d8fd;
	display: inline-block;
	height: 30px;
	width: 30px;
}

.node.start {
	background-color: #f56565;
}

.node.start:hover {
	background-color: #e53e3e;
}

.node.finish {
	background-color: #48bb78;
}

.node.finish:hover {
	background-color: #38a169;
}

.node.wall {
	-webkit-animation: wallAnimation 0.5s ease-out;
	        animation: wallAnimation 0.5s ease-out;
	background-color: #243e63;
}

.node.wall:hover {
	background-color: #203859;
}

@-webkit-keyframes wallAnimation {
	0% {
		background-color: #00000000;
	}
	100% {
		background-color: #243e63;
	}
}

@keyframes wallAnimation {
	0% {
		background-color: #00000000;
	}
	100% {
		background-color: #243e63;
	}
}

.node.visited {
	-webkit-animation: visitedAnimation 0.5s ease-out;
	        animation: visitedAnimation 0.5s ease-out;
	background-color: #6415ff;
}

@-webkit-keyframes visitedAnimation {
	0% {
		background-color: #00000000;
	}
	100% {
		background-color: #6415ff;
	}
}

@keyframes visitedAnimation {
	0% {
		background-color: #00000000;
	}
	100% {
		background-color: #6415ff;
	}
}

.node.path {
	-webkit-animation: pathAnimation 0.5s ease-out;
	        animation: pathAnimation 0.5s ease-out;
	background-color: #ecc94b;
}

@-webkit-keyframes pathAnimation {
	0% {
		background-color: #00000000;
	}
	100% {
		background-color: #ecc94b;
	}
}

@keyframes pathAnimation {
	0% {
		background-color: #00000000;
	}
	100% {
		background-color: #ecc94b;
	}
}

